// styles.scss

@import 'reset';

body {
  font: 100% Helvetica, sans-serif;
  background-color: #efefef;
}

@mixin transform($property) {
  -webkit-transform: $property;
      -ms-transform: $property;
          transform: $property;
}


.box { @include transform(rotate(30deg)); }

%message-shared {
  border: 1px solid #ccc;
  padding: 1em;
  color: #333;
}

.message {
  @extend %message-shared;
}

success {
  @extend %message-shared;
  border-color: green;
  font-size: 1em;
}

.error {
  @extend %message-shared;
  border-color: red;
}

.warning {
  @extend %message-shared;
  border-color: yellow;
}

.testing {
  width: percentage(5/7);
}

body {
  font-family: arial;
}

.container {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  min-height: 100vh;
}


@media (min-width: 768px) {
  .container {
    display: grid;
    grid-template-columns: 110px 1fr 110px;
    grid-template-rows: auto 1fr auto;

  }
}


header {
  grid-column: span 3;
  padding-bottom: 30px;
  padding-top: 30px;
  text-align: left;
  font-size: 1.4em;
  background-color: #369;
  color: white;
  width: 100%;
  padding-left: 2em;
}

main {
  display: flex;
  height: 100%;
  font-size: 2rem;
  position: relative;
  justify-content: center;  
  width: 90%;
}

.cente {
  border: 1px solid #fff;
  width: 30em;
  display: flex;
  padding-top: 1.5em;
  padding-left: 1.5em;
  padding-right: 1.5em;
  justify-content: space-around;
  margin-left: auto;
  margin-right: auto;
}
.left {
    width: 40%;
}

.left select {
    width: 100%;
}

input {
    text-align: right;
    width: 8em;

}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button { 
  opacity: 1;
}

.right select {
    width: 100%;
}
.right {
    width: 40%;
}

.net-warning {
    color: #9F6000;
    background-color: #FEEFB3;
    font-style:normal;
    font-weight:0.07em;
    box-shadow:1px 1px 3px #888;
    border: 1px solid;
    border-radius:.5em;
     font-size: 0.4em;
}
nav {
  background-color: #f90;
  padding: 20px;
}
 
aside {
  padding: 20px;
  background-color: #936;
}

footer {
  grid-column: span 3;
  padding-bottom: 30px;
  padding-top: 30px;
  text-align: center;
  font-size: 1.4em;
  background-color: #690;
  color: white;
  width: 100%;
}

h1 {
  margin-bottom: 1em;
  font-size: 1.3em;
  font-weight: bold;
}